* {
  margin: 0;
  padding: 0;
}
body {
  width: 100vw;
}
.App {
  margin-bottom: 4rem;
}
p {
  margin-top: 1rem;
}

.App-header {
  background-color: #282c34;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 2rem 1rem;
}
.App-header > h1 {
  text-align: center;
}
.objective > p {
  font-size: 1rem;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.doc {
  display: flex;
  flex-direction: column;
}
.right-col {
  display: flex;
  flex-direction: column;
  align-items: left;
  width: 100%;
}
.objective {
  margin-top: 1rem;
}
.left-col {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.marg,
.experience,
.education,
.Pro-rel-skills,
.skills-port,
.name,
.contact,
.objective,
.skills,
.left-col,
.right-col {
  margin-top: 1rem;
}
.exp1 {
  margin: 0 0 1rem 0;
}
.name,
.contact,
.objective,
.skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.name {
  margin: 1rem;
  line-height: 3.5rem;
  border-top: 3px solid rgba(0, 185, 174, 1);
  border-bottom: 3px solid rgba(0, 185, 174, 1);
}
.contact img {
  margin: 1rem;
  width: 25%;
}
.contact {
  margin: 2rem;
  border-bottom: 3px solid rgba(0, 185, 174, 1);
  padding-bottom: 3rem;
}
.skills {
  align-items: left;
}
.skills > h2 {
  border-bottom: 3px solid rgba(0, 185, 174, 1);
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}
.skills > ul,
ul {
  list-style: none;
  text-align: left;
}
.skills > ul > li {
  line-height: 2rem;
}
.experience,
.education,
.Pro-rel-skills,
.skills-port {
  display: flex;
  flex-direction: column;
  align-items: left;
  margin: 1rem 1rem;
}
.experience > h3,
.education > h3,
.Pro-rel-skills > h3,
.skills-port > h3 {
  margin-top: 1rem;
}
.experience > h2 {
  text-align: center;
}
.experience-h1-ed {
  margin: 0 1rem;
}
.experience-h1 {
  width: 90%;
  border-top: 3px solid rgba(0, 185, 174, 1);
  border-bottom: 3px solid rgba(0, 185, 174, 1);
  text-align: center;
}
#col-left-portfolio {
  word-wrap: break-word;
  width: 100%;
}
.col-right li {
  margin-top: 1rem;
}
.experience-ul > li,
.education-ul > li,
.Pro-rel-skills-ul > li,
.skills-port-ul > li {
  margin: 1rem 0;
}
.experience-h1 > h2 {
  margin: 1rem 0;
}
.vid {
  color: rgba(0, 185, 174, 1);
  font-weight: 700;
  cursor: pointer;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 5px;
  text-align: center;
  border: 3px solid rgba(0, 185, 174, 1);
  border-radius: 25px;
  width: 10rem;
}
#vid-antotech {
  width: 100%;
}
.vid:hover {
  text-decoration: underline;
}
vid:active {
  color: rgba(0, 185, 174, 1);
}
.skills-port {
  display: flex;
  flex-direction: column;
  word-wrap: break-word;
  width: 90%;
}
.Pro-rel-skills-ul > li > p {
  line-height: 1.5rem;
}
.skills-port > div {
  display: flex;
  flex-direction: column;
  width: 80%;
}
.skills-port-section {
  width: 90%;
  margin: 0 1rem;
}
.SvgButtons {
  display: flex;
  width: 90%;
  flex-direction: column;
}
svg {
  width: 100%;
}

.painZone-svg-left-right {
  width: 100%;
}
.painZone-svg-center {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 100%;
}
.sides {
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: row;
  height: 100%;
  justify-content: space-evenly;
}
.Rside,
.Lside {
  width: 50%;
  margin: 0 4px;
}

.singles {
  text-align: center;
  display: flex;
  flex-direction: row;
  height: 20%;
  width: 100%;
  justify-content: space-around;
}
.bParts-single {
  height: 40%;
  background-color: rgba(0, 185, 174, 1);
  margin-bottom: 3px;
  vertical-align: center;
  border-radius: 5px;
  cursor: pointer;
  color: white;
  width: 40%;
}
.bParts {
  padding: 0;
  background-color: rgba(0, 185, 174, 1);
  margin-right: 0;
  margin-bottom: 6px;
  vertical-align: middle;
  border-radius: 5px;
  cursor: pointer;
  color: white;
  line-height: 27px;
}
.exp {
  border-top: 3px solid rgba(0, 185, 174, 1);
  border-bottom: 3px solid rgba(0, 185, 174, 1);
  text-align: center;
  padding: 1rem 0;
}
#Hips,
#Jaw {
  padding: 3px;
}
